.variableDeclaration {
  position: absolute;
  height: 91.67%;
  width: 91.67%;
  top: 4.17%;
  right: 4.17%;
  bottom: 4.17%;
  left: 4.17%;
  border-radius: var(--br-xl);
  background-color: var(--color-skyblue);
}
.vectorIcon {
  position: absolute;
  height: 37.5%;
  width: 50%;
  top: 29.17%;
  right: 25%;
  bottom: 33.33%;
  left: 25%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.leadingIcon,
.selection {
  height: 24px;
  width: 24px;
  position: relative;
}
.leadingIcon {
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.employmentInformation {
  flex: 1;
  position: relative;
  line-height: 24px;
  display: inline-block;
  min-width: 133px;
}
.dropdownListItem,
.dropdownListItem1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.dropdownListItem1 {
  flex: 1;
  background-color: var(--color-deeppink-100);
  padding: var(--padding-5xs) var(--padding-xs);
  gap: var(--gap-xs);
}
.dropdownListItem {
  align-self: stretch;
}
.lineIcon {
  height: 35px;
  width: 2px;
  position: relative;
}
.lineWrapper,
.section2 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-5xl);
}
.section2 {
  align-self: stretch;
  flex-direction: column;
  padding: 0 var(--padding-xs) 0 0;
  text-align: left;
  font-size: var(--mobile-body-large-160-size);
  color: var(--color-gray-200);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 450px) {
  .dropdownListItem1 {
    flex-wrap: wrap;
  }
}
