.logo {
  height: 21.6px;
  width: 172px;
  position: relative;
}
.iconsChild,
.iconsInner,
.iconsItem {
  height: 24px;
  width: 24px;
  position: relative;
  min-height: 24px;
}
.twitterIcon {
  object-fit: cover;
}
.groupIcon,
.instagramIcon,
.shoppingIcons,
.twitterIcon {
  height: 24px;
  width: 24px;
  position: relative;
  min-height: 24px;
}
.icons {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.header,
.iconsWrapper,
.logoicons {
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
}
.iconsWrapper {
  width: 312px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-12xs) 0 0;
}
.header,
.logoicons {
  flex-direction: row;
  max-width: 100%;
}
.logoicons {
  flex: 1;
  justify-content: space-between;
  padding: 0 0 var(--padding-11xs-4);
  gap: var(--gap-xl);
}
.header {
  align-self: stretch;
  background-color: var(--color-darkslateblue-300);
  border: 1px solid var(--color-cornflowerblue-200);
  justify-content: flex-start;
  padding: var(--padding-4xl) var(--padding-28xl) var(--padding-3xl-6);
  top: 0;
  z-index: 99;
  position: sticky;
}
@media screen and (max-width: 750px) {
  .header {
    padding-left: var(--padding-4xl);
    padding-right: var(--padding-4xl);
    box-sizing: border-box;
  }
}
