.sectionReferences002Child {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  display: none;
}
.questionSectionsLists {
  height: 1482px;
  width: 366px;
  background-color: var(--color-darkslateblue-300);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 32px var(--padding-2xs) 0 35px;
  box-sizing: border-box;
  min-width: 366px;
  max-width: 100%;
  z-index: 2;
}
.logicalOperatorInner {
  flex: 1;
  flex-direction: column;
  padding: 60px 0 0;
  box-sizing: border-box;
  min-width: 625px;
  max-width: 100%;
}
.logicalOperator,
.logicalOperatorInner,
.sectionReferences002 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.logicalOperator {
  width: 1411px;
  flex-direction: row;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: 64px;
  max-width: 100%;
}
.sectionReferences002 {
  width: 100%;
  position: relative;
  background: linear-gradient(179.88deg, #092056, #000);
  overflow: hidden;
  flex-direction: column;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .questionSectionsLists {
    flex: 1;
  }
  .logicalOperator {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1025px) {
  .logicalOperatorInner {
    padding-top: 39px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .logicalOperatorInner {
    padding-top: 25px;
    box-sizing: border-box;
    min-width: 100%;
  }
  .logicalOperator {
    gap: var(--gap-13xl);
  }
}
@media screen and (max-width: 450px) {
  .questionSectionsLists {
    height: auto;
    min-width: 100%;
  }
  .logicalOperator {
    gap: var(--gap-base);
  }
}
