.span {
  color: var(--color-tomato);
}
.label1 {
  flex: 1;
  position: relative;
  line-height: 16px;
  display: inline-block;
  max-width: 100%;
}
.label {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  max-width: 100%;
}
.leadingIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 24px;
}
.prefix {
  align-self: stretch;
  width: 28px;
  display: none;
  white-space: nowrap;
}
.prefix,
.suffix,
.textContent {
  position: relative;
  line-height: 24px;
}
.textContent {
  flex: 1;
  letter-spacing: -0.5px;
  font-weight: 500;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  display: inline-block;
  max-width: 100%;
}
.suffix {
  width: 23px;
  text-align: right;
  display: none;
}
.textGroup {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
  max-width: 100%;
}
.trailingIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.compactText {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
  max-width: 100%;
}
.leadingIcon1 {
  height: 20px;
  width: 20px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 20px;
}
.label2 {
  align-self: stretch;
  flex: 1;
  position: relative;
  letter-spacing: 0.1px;
  line-height: 20px;
  font-weight: 600;
}
.trailingIcon1 {
  height: 20px;
  width: 20px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 20px;
}
.buttonInnerFrame {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding: 0 0 0 var(--padding-3xs);
  gap: var(--gap-5xs);
}
.minimumWidth {
  align-self: stretch;
  height: 0;
  position: relative;
}
.baseComponentMedium,
.buttonTertiary {
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
}
.baseComponentMedium {
  width: 64px;
  flex: 1;
  border-radius: var(--br-5xs);
  display: flex;
  align-items: center;
}
.buttonTertiary {
  height: 20px;
  display: none;
  align-items: flex-start;
  text-align: right;
  font-size: var(--mobile-button-medium-142-size);
  color: var(--primary-light-primary);
}
.assetsTextMessages,
.input,
.overlay {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.assetsTextMessages {
  flex: 1;
  flex-direction: row;
  align-items: center;
}
.input,
.overlay {
  align-self: stretch;
}
.overlay {
  border-radius: var(--br-5xs);
  background-color: var(--color-darkslateblue-400);
  flex-direction: row;
  align-items: center;
  padding: var(--padding-5xs) var(--padding-xs);
  box-sizing: border-box;
  font-size: var(--mobile-body-large-160-size);
  color: var(--text-light-secondary);
  font-family: var(--mobile-button-medium-142);
}
.input {
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-9xs);
}
.lineIcon {
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.textFieldCompact,
.valueComparer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.valueComparer {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-9xs) 0 var(--padding-8xs-1);
  box-sizing: border-box;
  margin-top: -1px;
}
.textFieldCompact {
  flex: 1;
  border-radius: var(--br-5xs);
  flex-direction: column;
  min-width: 304px;
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
