.rectangle {
  height: 22px;
  width: 22px;
  position: absolute;
  margin: 0 !important;
  top: calc(50% - 11px);
  left: calc(50% - 11px);
  border-radius: var(--br-xl);
  background-color: var(--color-darkslateblue-200);
}
.vectorIcon {
  height: 9px;
  width: 12px;
  display: none;
  z-index: 1;
}
.div,
.selection,
.vectorIcon {
  position: relative;
}
.div {
  width: 6px;
  line-height: 24px;
  font-weight: 600;
  display: inline-block;
  min-width: 6px;
  z-index: 1;
}
.selection {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-4xs);
}
.leadingIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.employmentInformation {
  flex: 1;
  position: relative;
  font-size: var(--mobile-body-large-160-size);
  line-height: 24px;
  text-align: left;
  display: inline-block;
  min-width: 110px;
}
.dropdownListItem,
.dropdownListItem1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.dropdownListItem1 {
  flex: 1;
  background-color: var(--color-deeppink-100);
  padding: var(--padding-5xs) var(--padding-xs);
  gap: var(--gap-xs);
}
.dropdownListItem {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  text-align: center;
  font-size: var(--mobile-button-medium-142-size);
  color: var(--color-gray-200);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 450px) {
  .dropdownListItem1 {
    flex-wrap: wrap;
  }
}
