@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --mobile-button-medium-142: Inter;

  /* font sizes */
  --mobile-body-large-160-size: 16px;
  --mobile-button-medium-142-size: 14px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-23xl: 42px;
  --font-size-6xl: 25px;
  --font-size-15xl: 34px;

  /* Colors */
  --color-cornflowerblue-100: #8dadf8;
  --color-cornflowerblue-200: rgba(141, 173, 248, 0.08);
  --color-white: #fff;
  --color-darkslateblue-100: #3a5187;
  --color-darkslateblue-200: #334a81;
  --color-darkslateblue-300: #0d2459;
  --color-darkslateblue-400: #0c1e48;
  --primary-light-primary: #e80071;
  --color-deeppink-100: rgba(232, 0, 113, 0);
  --color-gray-100: rgba(255, 255, 255, 0.16);
  --color-gray-200: rgba(255, 255, 255, 0.64);
  --color-gray-300: rgba(255, 255, 255, 0.36);
  --text-light-secondary: rgba(0, 19, 37, 0.64);
  --color-tomato: #fd5e5a;
  --color-skyblue: #68e1fd;

  /* Gaps */
  --gap-base: 16px;
  --gap-13xl: 32px;
  --gap-39xl: 58px;
  --gap-10xl: 29px;
  --gap-5xs: 8px;
  --gap-9xs: 4px;
  --gap-xs: 12px;
  --gap-xl: 20px;
  --gap-31xl: 50px;
  --gap-6xl: 25px;
  --gap-11xl-8: 30.8px;
  --gap-mini: 15px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-xs: 12px;
  --padding-11xs: 2px;
  --padding-12xs: 1px;
  --padding-9xs: 4px;
  --padding-8xs-1: 4.1px;
  --padding-5xs: 8px;
  --padding-3xs: 10px;
  --padding-5xl: 24px;
  --padding-55xl: 74px;
  --padding-55xl-5: 74.5px;
  --padding-base: 16px;
  --padding-7xs: 6px;
  --padding-10xs: 3px;
  --padding-39xl: 58px;
  --padding-39xl-5: 58.5px;
  --padding-8xs: 5px;
  --padding-2xs: 11px;
  --padding-4xs: 9px;
  --padding-6xs: 7px;
  --padding-4xl: 23px;
  --padding-28xl: 47px;
  --padding-3xl-6: 22.6px;
  --padding-11xs-4: 1.4px;

  /* Border radiuses */
  --br-5xs: 8px;
  --br-xl: 20px;
}
