.rectangle {
  height: 22px;
  width: 22px;
  position: absolute;
  margin: 0 !important;
  top: calc(50% - 11px);
  left: calc(50% - 11px);
  border-radius: var(--br-xl);
  background-color: var(--color-skyblue);
}
.vectorIcon {
  height: 9px;
  width: 12px;
  display: none;
  z-index: 1;
}
.div,
.selection,
.vectorIcon {
  position: relative;
}
.div {
  width: 9px;
  line-height: 24px;
  font-weight: 600;
  display: inline-block;
  min-width: 9px;
  z-index: 1;
}
.selection {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-6xs) 0 var(--padding-5xs);
}
.referenceIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.employmentInformation {
  flex: 1;
  position: relative;
  font-size: var(--mobile-body-large-160-size);
  line-height: 24px;
  font-weight: 500;
  color: var(--color-skyblue);
  text-align: left;
  display: inline-block;
  min-width: 71px;
}
.dropdownListItem,
.dropdownListItem1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.dropdownListItem1 {
  flex: 1;
  background-color: var(--color-deeppink-100);
  padding: var(--padding-5xs) var(--padding-xs);
  gap: var(--gap-xs);
}
.dropdownListItem {
  align-self: stretch;
}
.lineIcon {
  height: 35px;
  width: 2px;
  position: relative;
}
.lineWrapper,
.section1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-5xl);
}
.section1 {
  align-self: stretch;
  flex-direction: column;
  padding: 0 var(--padding-xs) 0 0;
  color: var(--color-white);
}
.lineIcon1 {
  position: absolute;
  top: 40px;
  left: 24px;
  width: 2px;
  height: 35px;
}
.section11 {
  align-self: stretch;
  height: 75px;
  position: relative;
  z-index: 2;
}
.lineIcon2 {
  position: absolute;
  top: 40px;
  left: 24px;
  width: 2px;
  height: 35px;
  z-index: 1;
}
.section12 {
  align-self: stretch;
  height: 75px;
  position: relative;
  z-index: 8;
}
.rectangle1 {
  height: 22px;
  width: 22px;
  position: absolute;
  margin: 0 !important;
  top: calc(50% - 11px);
  left: calc(50% - 11px);
  border-radius: var(--br-xl);
  background-color: var(--color-darkslateblue-200);
}
.div1,
.vectorIcon1 {
  position: relative;
  z-index: 1;
}
.vectorIcon1 {
  height: 9px;
  width: 12px;
  display: none;
}
.div1 {
  width: 15px;
  line-height: 24px;
  font-weight: 600;
  display: inline-block;
  min-width: 15px;
}
.selection1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-9xs) 0 var(--padding-8xs);
  position: relative;
}
.wheelChairIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.disabilitiesSpecial,
.requirements {
  margin: 0;
}
.employmentInformation1 {
  flex: 1;
  position: relative;
  font-size: var(--mobile-body-large-160-size);
  line-height: 24px;
  text-align: left;
  display: inline-block;
  min-width: 128px;
}
.dropdownListItem3 {
  background-color: var(--color-deeppink-100);
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-5xs) var(--padding-xs);
  gap: var(--gap-xs);
}
.dropdownListItem2,
.dropdownListItem3 {
  flex: 1;
  flex-direction: row;
}
.dropdownListItem2,
.itemSection,
.section13 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.section13 {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-xs) var(--padding-2xs) 0;
  z-index: 9;
}
.itemSection {
  flex: 1;
  flex-direction: column;
  text-align: center;
  font-size: var(--mobile-button-medium-142-size);
  color: var(--color-gray-200);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 450px) {
  .dropdownListItem1 {
    flex-wrap: wrap;
  }
  .section11 {
    height: auto;
    min-height: 75;
  }
  .dropdownListItem3 {
    flex-wrap: wrap;
  }
}
